import React, {useState} from 'react'
import s from './Header.module.css'
import {NavLink} from "react-router-dom";

function Header() {
const [menu,setMenu]=useState(false)
    console.log(menu)

    return (
        <div className={s.wrapper}>
            <div className={menu?s.menuActive:s.menu}>
                <button className={s.menuBtn} onClick={(e)=>setMenu(!menu)}></button>
                <div className={s.menuList}>
                    <NavLink to={'/pre-junior'} className={s.navLink}> PreJunior </NavLink>
                    <NavLink to={'/Junior'} className={s.navLink}> Junior </NavLink>
                    <NavLink to={'/Junior-plus'}className={s.navLink}> Junior Plus </NavLink>
                </div>

            </div>

        </div>
    )
}

export default Header
